import React, { Component } from 'react';
import _ from 'lodash';
import anime from './anime.min.js';

import { UniquePathsSolver } from './solver.js';

import './TwitchDeal.css';

const COLORS = {
  destination: '#0F0',
  grid: '#F6F5C7',
  path: '#017979',
  invalid: '#F57D6C',
}

class TwitchDeal extends Component {
  constructor(opts) {
    super(opts);
    this.m = 10;
    this.n = 10;
    this.solver = new UniquePathsSolver(this.m, this.n, this.drawPath, this.drawNext);
  }
  async componentDidMount() {
    await this.solver.solve();
    await this.drawPath([]);
  }

  drawPath = async (path) => {
    const animation = anime.timeline({
      duration: 1,
      easing: 'easeInOutQuad',
    });

    animation.add({
      targets: document.querySelectorAll('.boundary'),
      backgroundColor: '#000',
    });
    animation.add({
      targets: document.querySelectorAll('.cell'),
      backgroundColor: COLORS.grid,
    });
    animation.add({
      targets: path.map(p => document.getElementById(`${p[0]}\-${p[1]}`)),
      backgroundColor: COLORS.path,
    });

    await animation.finished;
  };

  drawNext = async (p, isInvalid) => {
    const animation = anime.timeline({
      duration: 1,
      easing: 'easeInOutQuad',
    });

    animation.add({
      targets: [document.getElementById(`${p[0]}\-${p[1]}`)],
      backgroundColor: isInvalid ? COLORS.invalid : COLORS.destination,
    });

    await animation.finished;
  };

  render() {
    const rows = [];
    for (const i in _.range(this.m)) {
      const cols = [];
      for (const j in _.range(this.n)) {
        const id = `${i}-${j}`;
        cols.push(<td className="cell" id={id} key={id}></td>);
      }
      const colBoundaryId = `${i}-${this.n}`;
      cols.push(<td className="boundary" id={colBoundaryId} key={colBoundaryId}></td>)
      rows.push(<tr key={i}>{cols}</tr>);
    }
    rows.push(
      <tr key={this.m}>{
        _.map(_.range(this.n + 1), j => {
          const id = `${this.m}-${j}`;
          return <td className="boundary" id={id} key={id}></td>;
        })
      }</tr>
    );

    return (
      <table>
        <tbody>{rows}</tbody>
      </table>
    );
  }
}



export default TwitchDeal;



